.container {
	max-width: 900px;
	margin: 0 auto;
}

.button {
	padding: 10px 6px;
	border-radius: 12px;
	width: 200px;
	color: white;
	background: #6213d9;
	font-size: 18px;
	font-style: italic;
	cursor: pointer;
	&:hover {
		background: #190438;
	}
}

.form {
	margin-bottom: 24px;
	text-align: center;
}

.header {
	margin: 12px;
	font-size: 24px;
	padding: 0 10px;
	@media (max-width: 400px) {
		display: flex;
		font-size: 14px;
	}
}
