.root {
	display: flex;
	align-items: center;
	justify-content: space-around;
	width: 33%;
	@media (max-width: 400px) {
		display: flex;
		justify-content: space-between;
		align-items: flex-start;
		width: 100%;
	}
}

.card {
	display: flex;
	font-size: 18px;
	text-align: center;
	// margin-bottom: 12px;
	cursor: pointer;
	perspective: 1000px;
	height: 33px;
	width: 170px;
	margin-right: 12px;
	@media (max-width: 400px) {
		height: 33px;
		width: 270px;
	}
	&.done {
		.cardInner {
			transform: rotateY(180deg);
			background: rgb(16, 216, 16);
		}
		// .cardFront {
		// 	opacity: 0;
		// }
		// .cardBack {
		// 	opacity: 1;
		// 	margin-top: -20px;
		// 	background: rgb(16, 216, 16);
		// }
	}
}

.cardFront,
.cardBack {
	position: absolute;
	width: 100%;
	height: 100%;
	-webkit-backface-visibility: hidden;
	backface-visibility: hidden;
	padding: 6px 12px;
}

.isRemembered {
	.cardFront,
	.cardBack {
		text-decoration: line-through;
	}
}
.cardInner {
	position: relative;
	background-color: transparent;
	width: 100%;
	height: 100%;
	text-align: center;
	transition: transform 0.6s;
	transform-style: preserve-3d;
	box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
}

.cardFront {
	background-color: #fff;
	color: black;
}

.cardBack {
	background-color: #1bb95e;
	color: white;
	transform: rotateY(180deg);
}

.icons {
	font-size: 26px;

	&:hover {
		color: #1bb95e;
	}
}
