.root {
	display: flex;
	flex-wrap: wrap;
	row-gap: 2px;
	max-width: 900px;
	margin: 0 auto 65px;
	@media (max-width: 400px) {
		display: flex;
		flex-direction: column;
		justify-content: space-around;
		width: auto;
	}
}

.form {
	margin-top: 24px;
	margin-bottom: 24px;
	text-align: center;
	max-width: 900px;
	margin: 24px auto;
}

.form_wrap {
	display: flex;
	justify-content: center;
	margin-bottom: 40px;
}

.card {
	margin-bottom: 55px;
}
